export enum CodeLanguage {
  bash = 'bash',
  js = 'js',
  scss = 'scss',
}

export class CodeSnippet {
  language: CodeLanguage;
  code: string;

  constructor(
    language: CodeLanguage,
    code: string,
  ) {
    this.language = language;
    this.code = code;
  }
}

export class CodeSnippetOnScss extends CodeSnippet {
  constructor(code: string) {
    super(CodeLanguage.scss, code);
  }
}
