import {
  CodeLanguage,
  CodeSnippet,
  CodeSnippetOnScss,
} from '../models/components/code-snippet';

export const miscSnippets = {

  installViaNpm: new CodeSnippet(
    CodeLanguage.bash,
    'npm install --save printio',
  ),

  howToUse: new CodeSnippet(
    CodeLanguage.js,
    String.raw
`import printio from 'printio';

var elementForExport = document.getElementById('my-element-id');
var exportMode = 'png-image'; // See ExportMode values table below
/*
  ExportMode values:
    'print',                Result: null
    'canvas',               Result: Canvas HTML element
    'png-image',            Result: PNG image HTML element
    'png-data-url',         Result: PNG image data url
    'download-png-image',   Result: null
    'jpg-image',            Result: JPG image HTML element
    'jpg-data-url',         Result: JPG image data url
    'download-jpg-image',   Result: null
*/

printio.exportElement(elementForExport, exportMode).then(
  function (exportResult) {

    /*
      - If exportMode equals to "print"
        then result will be "null"

      - If exportMode equals to "canvas"
        then result will be "Canvas HTML element"

      - If exportMode equals to "png-image"
        then result will be "PNG image HTML element"

      - If exportMode equals to "png-data-url"
        then result will be "PNG image data url"

      - If exportMode equals to "download-png-image"
        then result will be "null"

      - If exportMode equals to "jpg-image"
        then result will be "JPG image HTML element"

      - If exportMode equals to "jpg-data-url"
        then result will be "JPG image data url"

      - If exportMode equals to "download-jpg-image"
        then result will be "null"
    */

  },
);
`,
  ),

};
